@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'Roboto', 'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: #444;
  color: #ddd;
}

body {
  margin: 1em auto;
  max-width: 1200px;
}

.login-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 400px;
  margin: 0 auto;
}

.new-user-btn {
  padding: 5px 15px;
  font-size: 1rem;
  margin: 5px;
  border: 1px solid #777;
  color: #eee;
  background-color: #333;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
}

.new-user-btn:hover {
  background-color: #444;
}

.back-bar {
  display: flex;
  justify-content: flex-end;
}

.btn-container {
  margin: 5px;
}

.new-user-page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.new-user-form {
  display: flex;
  width: 800px;
  flex-direction: column;
}

.new-user-form input {
  padding: 5px 15px;
  font-size: 1rem;
  margin: 5px 0;
  border: 1px solid #777;
  color: #eee;
  background-color: #333;
  border-radius: 5px;
}

.new-user-form button {
  padding: 5px 15px;
  font-size: 1rem;
  border: 1px solid #777;
  color: #eee;
  background-color: #333;
  border-radius: 5px;
  cursor: pointer;
}

.new-user-form button:hover {
  background-color: #444;
}

.new-user-form label {
  margin-left: 5px;
}
